/* ==========================================================================
# Search opener - begin
========================================================================== */

.mkdf-search-opener {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    font-size: 15px;
    line-height: 1;
    margin: 0 10px;
    color: #fff;

    .mkdf-search-opener-wrapper {
        width: 36px;
        height: 36px;
        display: block;
        text-align: center;
        border: 1px solid #fff;
        border-radius: 3px;
        box-sizing: border-box;
        @include mkdfTransition(border-color .2s ease-out);
    
        > * {
            position: relative;
            display: inline-block;
            vertical-align: middle;
            line-height: inherit;
            top: 50%;
            @include mkdfTransform(translateY(-50%));
        
            &:before {
                display: block;
                line-height: inherit;
            }
        }
    }

    &:hover {
        color: $second-main-color;

        .mkdf-search-opener-wrapper {
            border-color: $second-main-color;
        }
    }
}

.mkdf-light-header .mkdf-page-header > div:not(.mkdf-sticky-header):not(.fixed) .mkdf-search-opener,
.mkdf-light-header .mkdf-top-bar .mkdf-search-opener {
    color: $header-light-color !important; //Color can be set inline from widget opener

    &:hover {
        color: $header-light-hover-color !important;
    }
}

.mkdf-dark-header .mkdf-page-header > div:not(.mkdf-sticky-header):not(.fixed) .mkdf-search-opener,
.mkdf-dark-header .mkdf-top-bar .mkdf-search-opener {
    color: $header-dark-color !important; //Color can be set inline from widget opener

    &:hover {
        color: $header-dark-hover-color !important;
    }
}
/* ==========================================================================
# Search opener - end
========================================================================== */